"use client"
import "leaflet/dist/leaflet.css"
import "leaflet-defaulticon-compatibility"
import L from "leaflet";

import { MapContainer, Marker, Popup, TileLayer } from "react-leaflet";
// Datos falsos de estudiantes en universidades rusas

delete L.Icon.Default.prototype._getIconUrl;

L.Icon.Default.mergeOptions({
  iconRetinaUrl: "/marker-icon-2x.png",
  iconUrl: "/marker-icon.png",
  shadowUrl: "/marker-shadow.png",
});

const students = [
  {
    name: "Anna Petrova",
    university: "Universidad Estatal de Moscú",
    major: "Física",
    coords: [55.7033, 37.5301],
  },
  {
    name: "Ivan Smirnov",
    university: "Universidad ITMO de San Petersburgo",
    major: "Ciencias de la Computación",
    coords: [59.9343, 30.3351],
  },
  {
    name: "Elena Kuznetsova",
    university: "Universidad Estatal de Novosibirsk",
    major: "Matemáticas",
    coords: [55.0302, 82.9204],
  },
  {
    name: "Dmitry Ivanov",
    university: "Universidad Politécnica de Tomsk",
    major: "Ingeniería Electrónica",
    coords: [56.501, 84.9925],
  },
  {
    name: "Sofia Morozova",
    university: "Universidad Federal de los Urales",
    major: "Arquitectura",
    coords: [56.8389, 60.6057],
  },
]

export default function StudentMap() {
  return (
    <div>
      <MapContainer
        center={[58.0, 40.0]}
        zoom={4}
        style={{ height: "500px", width: "100%", zIndex: 0 }}
      >
        <TileLayer
          url="https://tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a>'
        />
        {students.map((student, index) => (
          <Marker key={index} position={student.coords}>
            <Popup>
              <strong>{student.name}</strong>
              <br />
              {student.university}
              <br />
              Carrera: {student.major}
            </Popup>
          </Marker>
        ))}
      </MapContainer>
    </div>
  )
}
